.menuCon .rightMenu {
    float: right;
}

.menuCon .rightMenu-hidden{
    display: none;
}

.barsMenu {
    border: none;
    float: right;
    height: 32px;
    padding: 6px;
    margin-top: 16px;
    display: none;
    background: none;
}

.barsBtn {
    display: block;
    width: 20px;
    height: 2px;
    background: black;
    position: relative;
}

.barsBtn:after,
.barsBtn:before {
    content: attr(x);
    width: 20px;
    position: absolute;
    top: -8px;
    left: 0;
    height: 2px;
    background: black;
}

.barsBtn:after {
    top: auto;
    bottom: -8px;
}

.right-menu {
    padding-top: 10px;
}

